import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Post, BlogService } from '../blog.service';

@Component({
  selector: 'app-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.css']
})
export class ListComponent implements OnInit {

  posts: Post[];

  constructor(private blogService: BlogService, private router: Router) { }

  ngOnInit(): void {
    this.blogService.fetchPosts()
                    .then((val) => {
                      this.posts = val;
                      console.log(this.posts[0]);
                    });
  }

  clickedNewPost(): void {
    if (this.posts) {this.posts.sort(compare);}
    let newPostId = this.posts[this.posts.length-1].postid + 1;
    let post: Post = { 'postid': newPostId, 'created': new Date(), 'modified': new Date(), 'title': '', 'body': '' };
    this.blogService.setCurrentDraft(post);
    this.router.navigate(['edit', post.postid]);
  }

  onPostClick(event, post) {
    console.log("here with clicked post " + post.postid);
    this.blogService.setCurrentDraft(post);
    this.router.navigate(['edit', post.postid]);
    this.blogService.fetchPosts()
                    .then((val) => {
                        this.posts = val;
                        console.log("fetched posts after clicked one");
                    });
  }
}

// compare function for sorting results
function compare(a, b) {
  const aPostId = a.postid;
  const bPostId = b.postid;

  let comparison = 0;
  if (aPostId > bPostId) {
    comparison = 1;
  } else if (aPostId < bPostId) {
    comparison = -1;
  }
  return comparison;
}
