import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { HttpErrorResponse, HttpResponse } from '@angular/common/http';

import { Observable, throwError } from 'rxjs';
import { catchError, retry } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})

export class Post {
  postid: number;
  created: Date;
  modified: Date;
  title: string;
  body: string;
}

@Injectable()
export class BlogService {
  private posts: Post[];
  private currentDraft: Post;
  username: string;

  constructor(private http: HttpClient) {
    this.username = parseJWT(getCookie()).usr;
    console.log(this.username);
    console.log("NEW COMMENT");
  }

  fetchPosts(): Promise<Post[]> {
    return new Promise((resolve) => {
      let url = '/api/' + this.username;
      this.http.get<Post[]>(url)
                .subscribe(data => {
                                  this.posts = data;
                                  resolve(this.posts);
                                  console.log("inside fetchposts" + this.posts);
                      })
    });
  }

  getPost(postid: number): Promise<Post> {
    return new Promise((resolve) => {
      let url = '/api/' + this.username + '/' + postid;
      let thisPost: Post = null;
      console.log("inside get post in blog service");
      this.http.get<Post>(url).subscribe(data => {
                                  thisPost = data;
                                  resolve(thisPost);
                                  console.log("inside getpost" + thisPost);
                                }, error => {
                                  if (error.status == 404) {
                                    resolve(null);
                                  }
                                });
    });
  }

  newPost(post: Post): Promise<void> {
    return new Promise((resolve) => {
      let url = '/api/' + this.username + '/' + post.postid;
      this.http.post<Post>(url, {title: post.title, body: post.body}).subscribe(
                            next => console.log("successful new post"),
                            error => {
                              if (error.status == 201) {
                                console.log("successful create post");
                              } else {
                                console.log("error creating post");
                              }
                            }
                          );
      this.fetchPosts();
    });
  }

  updatePost(post: Post): Promise<void> {
    return new Promise((resolve) => {
      let url = '/api/' + this.username + '/' + post.postid;
      console.log('heres the username ' + this.username + ' and the post id' + post.postid + post.title);
      this.http.put<Post>(url, {title: post.title, body: post.body}).subscribe(
                          next => console.log("successful update post"),
                          error => {
                            if (error.status == 200) {
                              console.log("successful update post");
                            } else {
                              console.log("error updating post");
                            }
                          }
                        );
      this.fetchPosts();
    });
  }

  deletePost(postid: number): Promise<void> {
    return new Promise((resolve) => {
      let url = '/api/' + this.username + '/' + postid;
      this.http.delete<Post>(url).subscribe(
                          next => console.log("successful delete"),
                          error => {
                            if (error.status == 204) {
                              console.log("successful delete post");
                            } else {
                              console.log("error deleting post");
                            }
                          }
                        );
      this.fetchPosts();
    });
  }

  setCurrentDraft(post: Post): void {
    this.currentDraft = post;
  }

  getCurrentDraft(): Post {
    return this.currentDraft;
  }
}

function getCookie() {
  var name = 'jwt=';
  var i = document.cookie.indexOf(name);
  var cookie = '';
  if (i != -1) {
    i = i + 4;
    while(document.cookie.charAt(i) != ';' && i < document.cookie.length) {
      cookie = cookie + document.cookie.charAt(i);
      i++;
    }
  }
  return cookie;
}

function parseJWT(token)
{
  let base64Url = token.split('.')[1];
  let base64 = base64Url.replace(/-/g, '+').replace(/_/g, '/');
  return JSON.parse(atob(base64));
}
