import { Component, OnInit, Input } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { Post, BlogService } from '../blog.service';

@Component({
  selector: 'app-edit',
  templateUrl: './edit.component.html',
  styleUrls: ['./edit.component.css']
})
export class EditComponent implements OnInit {
  @Input() post: Post;

  constructor(private blogService: BlogService, private router: Router, private activatedRoute: ActivatedRoute,) { }

  ngOnInit(): void {
    this.activatedRoute.paramMap.subscribe(() => this.getPost());
    console.log("this is the post in init");
  }

  getPost(): void {
    let id = Number(this.activatedRoute.snapshot.paramMap.get('id'));
    this.post = this.blogService.getCurrentDraft();
    if (!this.post) {
      console.log("get current draft returned null, searching post id");
      this.blogService.getPost(id)
                      .then((val) => {
                        console.log("inside get post");
                        this.blogService.setCurrentDraft(val);
                        this.post = val;
                        console.log("heres the post" + this.post);
                      });
    } else if (this.post.postid != id) {
      console.log("get current draft returned different id, searching post id");
      this.blogService.getPost(id)
                      .then((val) => {
                        this.blogService.setCurrentDraft(val);
                        this.post = val;
                      });
    }
    console.log("this is the post in get post in edit");
  }

  onClickSave(): void {
    this.blogService.getPost(this.post.postid)
                      .then((val) => {
                        if (val == null) {
                          this.blogService.newPost(this.post)
                                            .then((val) => {
                                              this.blogService.setCurrentDraft(this.post);
                                            });
                          location.reload();
                        } else {
                          this.blogService.updatePost(this.post)
                                          .then();
                          location.reload();
                        }
                      });
  }

  onClickPreview(): void {
    this.blogService.setCurrentDraft(this.post);
    this.router.navigate(['preview', this.post.postid]);
  }

  onClickDelete(): void {
    this.blogService.deletePost(this.post.postid)
                    .then(next => {
                      console.log("successful delete");
                      this.router.navigate(['/']);
                    });
  }
}
